<template>
  <div id="home">
    <top-header v-on:titleChanged="updateTitle($event)" ></top-header>
    <h2>{{title}}</h2>
    <img src="../assets/logo.png">
    <hr></hr>
    <users v-bind:content="content"></users>
  </div>
</template>

<script>
// 局部注册组件
import Users from './Users'
import Header from './Header'

export default {
  name: 'home',
  data () {
    return {
      content: [
        {name:"傲慢与偏见", author:"简.奥斯汀",show:false},
        {name:"傲慢与偏见", author:"简.奥斯汀",show:false},
        {name:"傲慢与偏见", author:"简.奥斯汀",show:false},
        {name:"傲慢与偏见", author:"简.奥斯汀",show:false},
        {name:"傲慢与偏见", author:"简.奥斯汀",show:false}
      ],
      title: "这是一个标题"
    }
  },
  methods: {
    updateTitle: function(e) {
      this.title = e;
    }
  },
  components: {
    "users":Users,
    "top-header":Header
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



