<template>
  <div class="header" v-on:click="changeTitle" >
    <h2>这是一个头部组件 - top_header</h2>
    <ul>
      <li v-for="item in content">{{item}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'top-header',
  data () {
    return {
      content: ["导航A","导航B","导航C"]
    }
  },
  methods: {
    changeTitle: function() {
      this.$emit("titleChanged","子向父组件传值测试");
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .header {
    background: lightgreen;
    width: 100%;
    height: 100px;
    text-align: center;
  }
  h2 {
    color: #f00;
  }
  li {
    list-style: none;
    float: left;
    margin-right: 50px;
  }
</style>



