<template>
  <div id="zheXian">
    <div id="zheXianImg" style="width: 800px;height:400px;"></div>
    <div>众多加个中出现次数最多的值为：{{usualVal}}</div>
    <div>
      <h4>问题一：未能成功做到在初始进入页面即显示所需图表</h4>
      <h4>问题二：未能实现 echarts的异步数据加载更新，现用方法：setInterval() 每5秒调取接口刷新一次</h4>
    </div>
  </div>
</template>

<script>
  // 引入 echarts 主模块。
  import * as echarts from 'echarts/lib/echarts';
  // 引入折线图。
  import 'echarts/lib/chart/line';
  // 引入提示框组件、标题组件、工具箱组件。
  import 'echarts/lib/component/tooltip';
  import 'echarts/lib/component/title';
  import 'echarts/lib/component/toolbox';

  export default {
    name: 'zheXian',
    data() {
      return {
        timeArray: [
          ["22:50", 3067.2],
          ["22:52", 2067.2],
          ["22:54", 4067.2],
          ["22:56", 5069.2],
          ["22:63", 6069.2]
        ],
        xVal: ["22:50", "22:52", "22:54", "22:56", "22:58"],
        yVal: [],
        usualVal: 0
      }
    },
    mounted() {
      this.getHttp();
      this.drawLine();
      var that = this;
      setInterval(function() {
        that.getHttp();
        that.drawLine();
        that.maxCountElement(that.yVal);
      }, 5000);
    },
    methods: {
      drawLine() {
        var that = this
        // 基于准备好的dom，初始化 echarts 实例并绘制图表。
        echarts.init(document.getElementById('zheXianImg')).setOption({
          title: { text: '时间折线图' },
          tooltip: {},
          toolbox: {
            feature: {
              dataView: {},
              saveAsImage: {
                pixelRatio: 1000
              },
              restore: {}
            }
          },
          xAxis: {
            //坐标轴类型
            type: 'category',
            //配置类目名称。 可以逐一设置，具体看文档
            data: that.xVal,
            //配置 鼠标移入时显示的 阴影指示器'line' 直线指示器 'shadow' 阴影指示,器  'none' 无指示器
            axisPointer: {
              show: true, //show 必须写，开关的作用
              type: 'line'
            }
          },
          yAxis: [{
            type: 'value', //坐标轴类型
            max: 'dataMax', //最大值
            scale: true,
            axisLabel: { //坐标轴刻度标签的相关设置。
              formatter: '{value}' //字符串模板
            }
          }, ],
          series: [{
            type: 'line',
            smooth: true,
            data: this.timeArray
          }]
        });
      },
      getHttp() {
        this.$http.get("https://sim.aitaoec.com/api/v1/product/price?type=timeline&id=0")
          .then((data) => {
            // console.log(data);

            var _len = data.body.data.arraydata.length;
            this.xVal = [];
            this.yVal = [];
            for(var i = 0; i < _len; i++) {
              data.body.data.arraydata[i].pop();
              this.xVal = this.xVal.concat(data.body.data.arraydata[i][0]);
              this.yVal = this.yVal.concat(data.body.data.arraydata[i][1]);
            }
            // console.log(data.body.data.arraydata);
            this.timeArray = data.body.data.arraydata;
          })
      },
      maxCountElement(arr) {   
        var obj = {};  
        for(var i = 0; i < arr.length; i++) {      
          var key = arr[i];
          if(obj[key]) {       
            obj[key]++;      
          } else {        
            obj[key] = 1;     
          }    
        }    
        var maxCount = 0;  
        var maxElement = arr[0];  
        for(var key in obj) {      
          if(maxCount < obj[key]) {        
            maxCount = obj[key];       
            maxElement = key;      
          }    
        } 
        this.usualVal = maxElement;   // return "该数组中出现次数最多的元素:"+maxElement+"出现了"+maxCount+"次";  
          
      }
    },
    created() {
      
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


