<template>
  <div class="users">
    <h2>这是一个测试组件 - Users</h2>
    <ul>
      <li v-for="item in content" @click="item.show = !item.show">
        <h2>{{item.name}}</h2>
        <h3 v-show="item.show" >{{item.author}}</h3>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'users',
  // props: ["content"],
  props: {
    content: {
      type: Array,
      required: true
    }
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .users {
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    box-sizing: border-box;
  }
  h2 {
    color: #f00;
  }
  .users ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0;
  }
  li {
    height: 120px;
    margin: 10px;
    margin-bottom: 20px;
    list-style: none;
    border: solid 1px #333;
    flex-grow: 1;
    flex-basis: 200px;
  }
</style>



